import { Component } from '@angular/core'

@Component({
    templateUrl: './message.component.html',
    selector: 'app-message'
})

export class MessageComponent {
    messages = ["Hello", "Hi", "I'M TEST", "Me too", "OK", "Done!"];
    size = this.messages.length;
   
}



